.search input[type=text] {
    margin-top: 30px;
    background-image: url(https://image.flaticon.com/icons/png/512/711/711319.png);
    background-size: 12px;
    background-repeat: no-repeat;
    text-indent: 39%;
    height: 20px;
    width: 200px;
    align-items: center;
    padding-top: 3px;
    padding-bottom: 3px;
    background-color: whitesmoke;
    border-radius: 3px;
    border-width: 1px;
    background-position-y: 7px;
    background-position-x: 63px;
    border-color: #cac8c5;
    border-style: solid;
}
.search input[type=text]:focus{
  outline: none;
}
